.message
{
	align-items: center;
	border: 1px solid $hit-gray;
	border-radius: 8px;
	color: $dove-gray;
	display: flex;
	font-size: 14px;
	height: 1.5*$lh;
	justify-content: center;
	line-height: 37px;
	text-align: center;

	&__image
	{
		display: inline-block;
		margin-right: 10px;
	}
}

@include b
{
	.message
	{
		font-size: 18px;
	}
}