.thankyou
{
	display: grid;
	grid-row-gap: 1*$lh;

	&__icon
	{
		display: flex;
		flex-direction: column;
		text-align: center;
	}

	&__image
	{
		display: inline-block;
		height: 4*$lh;
		width: auto;
	}

	&__title
	{
		display: inline-block;
		font-size: 32px;
		font-weight: 700;
		line-height: 1.25*$lh;
		margin-top: 1*$lh;
	}

	&__subtitle
	{
		font-size: 20px;
		font-weight: 700;
		line-height: 1*$lh;
		margin-bottom: .5*$lh;
	}

	&__edit-image
	{
		display: inline-block;
		vertical-align: baseline;
	}

	&__data-table
	{
		display: grid;
		grid-column-gap: .5*$lh;
		grid-row-gap: .5*$lh;
	}

	&__text
	{
		border-top: 1px solid $gallery;
		padding-top: 1*$lh;
	}

}

@include a
{
	.thankyou
	{
		&__data-table
		{
			grid-template-columns: 1fr 1fr;
		}
	}
}

@include b
{
	.thankyou
	{
		grid-column-gap: 1*$lh;
		grid-row-gap: 2*$lh;
		grid-template-columns: 1fr 1fr;
		padding: 1*$lh;

		&__icon
		{
			grid-column: 1/-1;
			margin-bottom: 1*$lh;
		}

		&__image
		{
			height: 6*$lh;
		}

		&__title
		{
			font-size: 48px;
		}

		&__subtitle
		{
			font-size: 26px;
			line-height: 1.25*$lh;
			margin-bottom: 1.25*$lh;
		}

		&__text
		{
			grid-column: 1/-1;
			padding-top: 2*$lh;
		}
	}
}