.footer
{
	background-color: $cerulean;
	padding: 1*$lh 0;
	text-align: center;

	&__inner
	{
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin: 0 auto;
		width: 90%;
	}

	&__logo
	{
		height: .75*$lh;
		margin-bottom: .5*$lh;
	}

	&__text
	{
		color: $white;
	}

	&__nowrap
	{
		white-space: nowrap;
	}

	&__tel
	{
		font-weight: 700;
	}

	&__separator
	{
		display: none;
	}

}

@include l
{
	.footer
	{
		padding: 2*$lh 0;

		&__logo
		{
			height: 1*$lh;
			margin-bottom: .75*$lh;
		}

		&__text
		{
			align-items: center;
			display: flex;
			justify-content: center;
		}

		&__separator
		{
			display: block;
			padding: 0 .25*$lh;
		}
	}
}
