$s2-arrow-color: $black;
$s2-background-color: none;
$s2-border-color: $iron;
$s2-border-radius: 8px;
$s2-placeholder-color: $nevada;

$s2-list-background-color: $white;
$s2-list-border-color: $iron;
$s2-list-border-radius: 8px;

@import 'select2/core';

//
// Input
// 



// Normal

$height: 2*$lh;
$padding: .5*$lh;

select + .select2
{
	font-family: $ff1;
	font-size: 16px;
	min-width: 180px;
	width: auto !important;

	.select2-selection
	{
		height: $height;
		line-height: $height - 2px;
		padding: 0 $padding;
	}

	.select2-selection__arrow
	{
		height: $height - 2px;
		right: $padding;
	}
}

// Small

$height: 1.25*$lh;
$padding: .25*$lh;

select.select2--size-small + .select2
{
	font-size: 14px;

	.select2-selection
	{
		height: $height;
		line-height: $height - 2px;
		padding: 0 $padding;
	}

	.select2-selection__arrow
	{
		height: $height - 2px;
		right: $padding;
	}
}

// Big

$height: 2.5*$lh;
$padding: 1*$lh;

select.select2--size-big + .select2
{
	.select2-selection
	{
		height: $height;
		line-height: $height - 2px;
		padding: 0 $padding;
	}

	.select2-selection__arrow
	{
		height: $height - 2px;
		right: $padding;
	}
}

select.select2--display-block + .select2
{
	display: block;
	width: 100% !important;
}

//
// Results
//

.select2-container
{
	font-family: $ff1;

	.select2-results__option--highlighted[aria-selected]
	{
		background: $alabaster;
	}
}

// 
// Material 
//

select.select2 + .select2-container--material
{
	border-bottom: 1px solid $tower-gray;
	font-size: 16px;
	padding: 0;
	width: 100% !important;

	.select2-selection
	{
		padding: 0 !important;
	}

	.select2-selection__rendered
	{
		padding: 0 !important;
		padding-left: .25*$lh !important;
	}

	.select2-selection__arrow
	{
		position: absolute;
		right: 1px;
		top: 1px;
		width: 20px;

		b
		{
			border-color: $s2-arrow-color transparent transparent transparent;
			border-style: solid;
			border-width: 5px 4px 0 4px;
			height: 0;
			left: 50%;
			margin-left: -4px;
			margin-top: -2px;
			position: absolute;
			top: 50%;
			width: 0;
		}
	}
}


select.select2 + .select2-container--material.select2-container--open
{
	.select2-selection--single
	{
		.select2-selection__arrow
		{
			b
			{
				border-color: transparent transparent $s2-arrow-color transparent;
				border-width: 0 4px 5px 4px;
			}
		}
	}
}
