.steps-header
{
	padding: 1*$lh 0;

	&__inner
	{
		margin: 0 auto;
		width: 90%;
	}

	&__title
	{
		font-family: $ff2;
		font-size: 28px;
		font-weight: 300;
		line-height: 1.25*$lh;
		margin-bottom: 1*$lh;
		text-align: center;
	}

	&__list
	{
		position: relative;

		&::before
		{
			background: $hit-gray;
			content: '';
			display: block;
			height: 1px;
			left: 50%;
			position: absolute;
			top: 14px;
			transform: translateX(-50%);
			width: 100%;
			z-index: 1;
		}
	}

	&__item
	{
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&__number
	{
		background: $white;
		padding: 0 .75*$lh;
		position: relative;
		z-index: 2;
	}

	&__circle
	{
		align-items: center;
		background: $hit-gray;
		border-radius: 50%;
		color: $white;
		display: flex;
		font-size: 18px;
		font-weight: 700;
		height: 28px;
		justify-content: center;
		line-height: 18px;
		text-align: center;
		transition: background .3s;
		width: 28px;
	}

	&__text
	{
		color: $hit-gray;
		font-size: 18px;
		margin-top: .25*$lh;
	}

	&__item--state-active
	{
		.steps-header__circle
		{
			background: $cerulean;
		}

		.steps-header__text
		{
			color: $cerulean;
			font-weight: 700;
		}
	}

}

@include osm
{
	.steps-header
	{
		&__item
		{
			display: none;
		}

		&__item--state-active
		{
			display: flex;
		}
	}
}

@include l
{
	.steps-header
	{
		padding: 2*$lh 0;

		&__inner
		{
			width: 864px;
		}

		&__title
		{
			font-size: 62px;
			margin-bottom: 2.5*$lh;
		}

		&__list
		{
			display: flex;
			justify-content: space-around;

			&::before
			{
				width: 66%;
			}
		}

		&__item
		{
			width: 33%;
		}
	}
}
