.select2-selection--single
{
	background-color: $s2-background-color;
	border: 1px solid $s2-border-color;
	border-radius: $s2-border-radius;
	display: block;
	text-align: left;

	.select2-selection__rendered
	{
		color: inherit;
	}

	.select2-selection__clear
	{
		cursor: pointer;
		float: right;
		font-weight: bold;
	}

	.select2-selection__placeholder
	{
		color: $s2-placeholder-color;
	}

	.select2-selection__arrow
	{
		position: absolute;
		right: 1px;
		top: 1px;
		width: 20px;

		b
		{
			border-color: $s2-arrow-color transparent transparent transparent;
			border-style: solid;
			border-width: 5px 4px 0 4px;
			height: 0;
			left: 50%;
			margin-left: -4px;
			margin-top: -2px;
			position: absolute;
			top: 50%;
			width: 0;
		}
	}
}

&[dir="rtl"] {
	.select2-selection--single {
		.select2-selection__clear {
			float: left;
		}

		.select2-selection__arrow {
			left: 1px;
			right: auto;
		}
	}
}

&.select2-container--disabled
{
	.select2-selection--single
	{
		background-color: $white;
		cursor: default;

		.select2-selection__clear
		{
			display: none;
		}
	}
}

&.select2-container--open
{
	.select2-selection--single
	{
		.select2-selection__arrow
		{
			b
			{
				border-color: transparent transparent $s2-arrow-color transparent;
				border-width: 0 4px 5px 4px;
			}
		}
	}
}
