.input-wrapper
{
	//background: #fff;
	position: relative;
}

.input
{
	border-bottom: 1px solid $tower-gray;
	color: $woodsmoke;
	display: block;
	font-size: 16px;
	height: 2*$lh;
	line-height: 1.5*$lh;
	padding-left: .25*$lh;
	padding-top: .5*$lh;
	width: 100%;
}

.input + .label
{
	color: $nevada;
	display: block;
	left: .25*$lh;
	position: absolute;
	top: 1*$lh;
	transition: transform .2s ease-out;
}

.input.active + .label,
.input:focus + .label,
.input:not(:placeholder-shown) + .label
{
	transform: translateY(-150%) scale(.75);
	transform-origin: .25*$lh 0;
}

.input:disabled
{
	border-color: rgba(255,255,255,0) !important;
}



.label-checkbox
{
	align-items: center;
	display: flex;

	.checkbox
	{
		margin-right: 8px;
	}

	a
	{
		color: $black;
		text-decoration: underline;
	}
}