//
// Main Layout
//

.plans-item
{
	background: $white;
	border-radius: 8px;
	box-shadow: 0 7px 7px rgba(0, 0, 0, .04);
	display: grid;

	&__information
	{
		display: none;
	}
}

@include c
{
	.plans-item
	{
		grid-template-columns: 336px auto;

		&__information
		{
			grid-column: span 2;
		}
	}
}

//
// Information Open
//

.plans-item--information-open
{
	.plans-item__information
	{
		display: block !important;
	}

	.plans-item__description
	{
		border-bottom: 1px solid $porcelain;
	}

	.plans-item__more-info
	{
		&::after
		{
			transform: translateY(-50%) rotate(180deg) !important;
		}
	}
}

@include c
{
	.plans-item--information-open
	{
		.plans-item__header
		{
			border-radius: 8px 0 0 0;
		}
	}
}


//
// Header
//

.plans-item
{
	&__header
	{
		border-radius: 8px 8px 0 0;
		color: $white;
		display: grid;
		grid-row-gap: .5*$lh;
		grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
		padding: .75*$lh;
	}

	&__title
	{
		display: block;
		font-size: 24px;
		font-weight: 700;
		grid-column: 1/2;
		grid-row: 1/2;
		line-height: 1*$lh;
		white-space: nowrap;

		small
		{
			display: block;
			font-size: 14px;
			font-weight: 400;
		}
	}

	&__channels
	{
		grid-column: 1/3;
		line-height: .75*$lh;
	}

	&__button-wrapper
	{
		grid-column: 2/3;
		grid-row: 1/2;
		justify-self: end;
	}
}

@include b
{
	.plans-item
	{
		&__title
		{
			font-size: 54px;
			line-height: 1.5*$lh;

			small
			{
				font-size: 18px;
				padding-left: 10px;
			}
		}

		&__title--size-medium
		{
			font-size: 44px;
		}

		&__channels
		{
			font-size: 22px;
			line-height: 1*$lh;
		}

	}
}

@include c
{
	.plans-item
	{

		&__header
		{
			align-items: start;
			background: green;
			border-radius: 8px 0 0 8px;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding: 1.5*$lh 1.25*$lh;
		}
	}
}

//
// Description
//

.plans-item
{
	&__description-header
	{
		align-items: center;
		border-bottom: 1px solid $porcelain;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: .75*$lh;
	}

	&__more-info
	{
		display: inline-block;
		font-size: 14px;
		margin-top: .5*$lh;
		padding-right: 20px;
		position: relative;

		&::after
		{
			border-color: $woodsmoke transparent transparent transparent;
			border-style: solid;
			border-width: 5px 5px 0 5px;
			content: '';
			display: block;
			height: 0;
			position: absolute;
			right: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 0;
		}
	}

	&__price
	{
		font-size: 32px;
		line-height: 1.25*$lh;
	}

	&__price-number
	{
		display: inline-block;
		font-weight: 700;
		margin: 0 5px;
	}

	&__price-symbol
	{
		font-weight: 700;
	}

	&__price-symbol,
	&__price-lapse
	{
		font-size: .56em;
		position: relative;
		top: -5px;
		vertical-align: text-top;
	}

	&__description-content
	{
		color: $nevada;
		font-size: 14px;
		line-height: .875*$lh;
		padding: .75*$lh;

		ul
		{
			list-style: '• ';
			list-style-position: inside;
		}

		small
		{
			display: block;
			font-size: 12px;
			margin-top: 1*$lh;
		}
	}

}

@include a
{
	.plans-item
	{

		&__description-header
		{
			flex-direction: row;
			justify-content: space-between;
		}

		&__more-info
		{
			margin: 0;
		}
	}
}

@include c
{
	.plans-item
	{
		&__description-header
		{
			grid-template-columns: auto auto;
			padding: 1.25*$lh 1.5*$lh;
		}

		&__price
		{
			font-size: 52px;
			line-height: 2*$lh;
		}

		&__price-number
		{
			margin: 0 10px;
		}

		&__description-content
		{
			padding: 1*$lh 1.5*$lh;
		}
	}
}

//
// Information
//

.plans-item
{
	&__compare
	{
		align-items: center;
		border-bottom: 1px solid $porcelain;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: .75*$lh;

		& > *:first-child
		{
			margin-bottom: .5*$lh;
		}
	}

	&__include
	{
		display: grid;
		grid-column-gap: .25*$lh;
		grid-row-gap: .75*$lh;
		padding: .75*$lh;
	}

	&__box
	{
		border: 1px solid $iron;
		border-radius: 8px;
		padding: 1*$lh 0.25*$lh;
		text-align: center;
	}

	&__box-title
	{
		color: $nevada;
		font-weight: 700;
	}

	&__box-number
	{
		font-size: 24px;
		font-weight: 700;
	}

	&__include-title
	{
		font-size: 20px;
		font-weight: 700;
		text-align: center;
	}

	&__include-channels
	{
		align-items: center;
		display: flex;
		flex-direction: column;
		justify-content: end;

		> *:first-child
		{
			margin-bottom: .5*$lh;
		}
	}

	&__include-content
	{
		color: $nevada;
		font-size: 12px;
		line-height: .875*$lh;

		h4
		{
			color: $black;
			font-size: 16px;
			font-weight: 700;
			margin-bottom: .5*$lh;
		}

		ul
		{
			list-style-image: url('../images/check.svg');
			list-style-position: inside;
		}
	}

}

@include a
{
	.plans-item
	{
		&__compare
		{
			align-items: center;
			flex-direction: row;
			justify-content: space-between;

			& > *:first-child
			{
				margin-bottom: 0;
			}
		}

		&__include
		{
			grid-template-columns: 1fr 1fr;
			padding: 1.25*$lh;
		}

		&__include-title
		{
			grid-column: span 2;
		}

		&__include-channels
		{
			align-items: center;
			flex-direction: row;
			grid-column: span 2;
			justify-content: space-between;

			> *:first-child
			{
				margin-bottom: 0;
			}
		}

		&__include-content
		{
			grid-column: span 2;
		}
	}
}

@include b
{
	.plans-item
	{
		&__compare
		{
			padding: 1.25*$lh;
		}

		&__include-title
		{
			font-size: 26px;
			font-weight: 700;
		}
	}
}

@include c
{
	.plans-item
	{
		&__include
		{
			grid-column-gap: .25*$lh;
			grid-row-gap: 1.5*$lh;
			grid-template-columns: 1fr 1fr 1fr 1fr;
			padding: 1.25*$lh;
		}

		&__include-title
		{
			grid-column: span 2;
			justify-self: start;
		}

		&__include-channels
		{
			grid-column: span 2;
			justify-self: end;

			> *:first-child
			{
				margin-right: 20px;
			}
		}

		&__include-content
		{
			grid-column: span 4;
		}
	}
}

