//
// Colors
//

.plans-item--color-gold
{
	.plans-item__header
	{
		background: $anzac;
	}
}

.plans-item--color-silver
{
	.plans-item__header
	{
		background: $nobel;
	}
}

.plans-item--color-bronze
{
	.plans-item__header
	{
		background: $brandy-punch;
	}
}
