.button
{
	background: $woodsmoke;
	border-radius: 8px;
	color: #fff;
	cursor: pointer;
	display: inline-block;
	font-size: 18px;
	height: 1.5*$lh;
	line-height: 1.5*$lh;
	padding: 0 1*$lh;

	&:hover
	{
		background: lighten($woodsmoke, 5%);
	}
}

.button--size-small
{
	font-size: 14px;
	height: 1.25*$lh;
	line-height: 1.25*$lh;
	padding: 0 .75*$lh;
}

@include os
{
	.button
	{
		font-size: 14px;
		height: 1.25*$lh;
		line-height: 1.25*$lh;
		padding: 0 .75*$lh;
	}
}
