html
{
	overflow-x: hidden;
}

body
{
	background: $white;
	color: $woodsmoke;
	font-family: $ff1;
	font-variant-ligatures: none;
	font-weight: 400;
	line-height: $lh;
	min-width: $min-width;
	overflow-x: hidden;
}

a
{
	color: inherit;
	text-decoration: none;
}

pre
{
	background: $white;
	color: $woodsmoke;
	font-family: monospace;
}

b,
strong
{
	font-weight: 700;
}