.form
{
	display: grid;
	grid-row-gap: .75*$lh;

	&__title
	{
		font-size: 20px;
		font-weight: 700;
		line-height: 1*$lh;
	}

	&__row--margin-extra
	{
		margin-bottom: 1*$lh;
	}

	&__row--selected-plan
	{
		align-items: center;
		display: flex;
		justify-content: start;

		> *:first-child
		{
			margin-right: 10px;
		}
	}

	&__row--size-full
	{
		grid-column: -1/1;
	}

	&__seleted-plan-text
	{
		font-size: 14px;
		font-weight: 700;
	}
}

@include b
{
	.form
	{
		grid-column-gap: 1.25*$lh;
		//grid-row-gap: 1*$lh;
		grid-template-columns: 1fr 1fr;

		&__title
		{
			font-size: 26px;
			line-height: 1.25*$lh;
		}
	}
}
